import React from "react"
import SwiperCore, { Navigation, A11y } from "swiper"
import { Swiper, SwiperSlide } from "swiper/react"
import styled from "styled-components"
import { useTheme } from "@material-ui/core/styles"
import useMediaQuery from "@material-ui/core/useMediaQuery"
import { PlainLink } from "@/components/Link"

const RelatedCardWrapper = styled.div`
  ${({ theme, enableImage }) => `
    img {
        width: 100%;
        max-height: 200px;
        object-fit: cover;
    }

    .card {
      min-height: 64px;
      background-color: ${theme.palette.background.paper};
      padding: ${enableImage ? 0 : theme.spacing(2.5)}px;
      box-shadow: ${enableImage ? `none` : `5px 5px rgb(0 0 0 / 10%)`};
    }

    .card:hover {
      .title {
        transition: 0.3s;
        text-decoration: none;
        border-bottom: 3px solid ${theme.palette.primary.main};
      }
    }

    .image-placeholder {
      background: ${theme.palette.primary.main};
      box-shadow: 5px 5px rgb(0 0 0 / 10%);
      margin-bottom: ${theme.spacing(1)}px;
    }

    .title {
        display: inline-block;
        font-family: "Noto Serif TC", serif;
        font-weight: 900;
        font-size: 1.25rem;
        border-bottom: 3px solid ${theme.palette.background.paper};
    }

    .sub-title {
        font-size: 0.875rem;
    }

    .swiper-container {
      padding: 15px;
    }

    .swiper-button-next,
    .swiper-button-prev {
        position: absolute;
        top: 30%;
        width: 64px;
        height: 64px;
        border: 1px ${theme.palette.primary.main} solid;
        z-index: 10;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: white;
        color: black;
    }

    ${theme.breakpoints.up("viewport7")} {
      .swiper-button-next,
      .swiper-button-prev {
        border: none;
        background-color: transparent;
        color: ${theme.palette.secondary.main};
      }
    }


    .swiper-button-prev,
    .swiper-container-rtl .swiper-button-next {
        left: -36px;
        right: auto;
        border-radius: 0 64px 64px 0;
    }

    .swiper-button-next,
    .swiper-container-rtl .swiper-button-prev {
        right: -36px;
        left: auto;
        border-radius: 64px 0 0 64px;
    }

    .swiper-button-prev:after,
    .swiper-container-rtl .swiper-button-next:after {
        content: 'prev';
        position: absolute;
        right: 14px;
    }

    .swiper-button-next:after,
    .swiper-container-rtl .swiper-button-prev:after {
        content: 'next';
        position: absolute;
        left: 14px;
    }

    ${theme.breakpoints.up("viewport7")} {
      .swiper-button-prev:after,
      .swiper-container-rtl .swiper-button-next:after {
          right: 18px;
      }


      .swiper-button-next:after,
      .swiper-container-rtl .swiper-button-prev:after {
          content: 'next';
          position: absolute;
          left: 18px;
      }
    }

    .swiper-button-next:after,
    .swiper-button-prev:after {
        font-family: swiper-icons;
        font-size: 16px;
        text-transform: none!important;
        letter-spacing: 0;
        text-transform: none;
        font-variant: initial;
        line-height: 1;
    }
`}
`

// Import Swiper styles
import "swiper/swiper.scss"
import "swiper/components/navigation/navigation.scss"

// install Swiper components
SwiperCore.use([Navigation, A11y])

const RelatedCard = ({ items, enableImage = false }) => {
  const theme = useTheme()
  const viewport7 = useMediaQuery(theme.breakpoints.up("viewport7"))

  return (
    <RelatedCardWrapper enableImage={enableImage}>
      <Swiper
        loop={true}
        centeredSlides={true}
        spaceBetween={35}
        slidesPerView={viewport7 ? 3 : 1}
        navigation
      >
        {items.map(item => {
          const { id, name, imgUrl, to, countText } = item
          return (
            <SwiperSlide key={id}>
              <PlainLink to={to}>
                <div className="card">
                  {enableImage && (
                    <div className="image-placeholder">
                      {imgUrl && <img src={imgUrl} />}
                    </div>
                  )}
                  <div className="title">{name}</div>
                  <div className="sub-title">{countText}</div>
                </div>
              </PlainLink>
            </SwiperSlide>
          )
        })}
      </Swiper>
    </RelatedCardWrapper>
  )
}

export default RelatedCard
