import React from "react"
import styled from "styled-components"
import SwiperCore, { Pagination, A11y } from "swiper"
import { Swiper, SwiperSlide } from "swiper/react"

const FullWidthWrapper = styled.div`
  ${({ theme }) => `
  padding: ${theme.spacing(7)}px ${theme.spacing(2)}px ${theme.spacing(3)}px;
  background-color: #232326;
  min-height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: end;

  .header {
    margin: 0 auto;
    text-align: center;
    font-weight: 900;
    color: ${theme.palette.background.paper};
  }

  .header__Name {
    font-family: "Noto Serif TC", serif;
    font-size: 2rem;
    ${theme.breakpoints.up("viewport7")} {
      fontSize: 2.5rem;
    }
  }

  .header__DefendantCount {
    margin-top: ${theme.spacing(1)}px;
    font-size: 90%;
    ${theme.breakpoints.up("viewport7")} {
      font-size:  100%;
    }
  }
`}
`

const FullWidthImageWrapper = styled.div`
  ${({ theme }) => `
  height: 60vh;
  
  .header {
    margin: 0 auto;
    padding: 0 ${theme.spacing(2)}px;
    text-align: center;
    font-weight: 900;
    color: ${theme.palette.background.paper};
    position: relative;
    bottom: 100px;
    z-index: 1;
    ${theme.breakpoints.up("viewport7")} {
      bottom: 110px;
    }
  }

  .header__Name {
    color: ${theme.palette.background.paper};
    font-family: "Noto Serif TC", serif;
    font-size: 1.5rem;
    ${theme.breakpoints.up("viewport7")} {
      font-size:  2.5rem;
    }
  }

  .header__DefendantCount {
    color: ${theme.palette.background.paper};
    font-size: 90%;
    ${theme.breakpoints.up("viewport7")} {
      font-size:  100%;
    }
  }

  .swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets {
    bottom: 10px;
    left: 0;
    width: 100%;
}

  .swiper-pagination {
    position: absolute;
    text-align: center;
    transition: 300ms opacity;
    transform: translate3d(0, 0, 0);
    z-index: 10;
}

  .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
    display: inline-block;
    border-radius: 100%;
    background: #000;
    opacity: 0.2;
    background: white;
  }

  .swiper-pagination-bullet-active {
    background: white;
    opacity: 1;
  }

  .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 4px;
}
`}
`

const ImageWrapper = styled.div`
  ${({ theme, imgUrl, align, alignMobile }) => `
    background-size: cover;
    background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5) 85%, ${
      theme.palette.primary.contrastText
    } 100%), url(${imgUrl});
    background-repeat: no-repeat;
    background-position: center ${alignMobile || "bottom"};
    height: ${imgUrl ? "60vh" : "auto"};

    ${theme.breakpoints.up("viewport7")} {
      background-position: center ${align || "bottom"};
  }
`}
`

// Import Swiper styles
import "swiper/swiper.scss"
import "swiper/components/pagination/pagination.scss"

// install Swiper components
SwiperCore.use([Pagination, A11y])

const FullWidthImage = ({ children, images = [] }) => {
  if (!images.length) {
    return <FullWidthWrapper>{children}</FullWidthWrapper>
  }
  if (images.length === 1) {
    return (
      <FullWidthImageWrapper>
        <ImageWrapper
          imgUrl={images[0].url}
          alignMobile={images[0].alignMobile}
          align={images[0].align}
        />
        {children}
      </FullWidthImageWrapper>
    )
  }

  return (
    <FullWidthImageWrapper>
      <Swiper
        loop={images.length ? true : false}
        centeredSlides={true}
        spaceBetween={0}
        slidesPerView={1}
        pagination={{ clickable: true }}
      >
        {images.map(image => {
          return (
            <SwiperSlide key={image.url}>
              <ImageWrapper
                imgUrl={image.url}
                alignMobile={image.alignMobile}
                align={image.align}
              />
            </SwiperSlide>
          )
        })}
      </Swiper>
      {children}
    </FullWidthImageWrapper>
  )
}

export default FullWidthImage
